#experience-view {
  height: 100vh;
  display: flex;
  flex-direction: row;
}

#experience-text {
  width: 17vw;
}

#experience-jobs {
  margin-left: auto;
  margin-right: 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

#experience-rect {
  margin-top: 10px;

  rect {
    fill: #aaaaaa;
  }
}

.experience-logos {
  width: 14vw;
  height: 14vw;
  // filter: drop-shadow(0 10px 50px rgba(0,0,0, 0.25)) drop-shadow(0 -10px 50px rgba(0,0,0, 0.25)) drop-shadow(10px 0 50px rgba(0,0,0, 0.25)) drop-shadow(-10px 0 50px rgba(0,0,0, 0.25));
  margin: 0 10px;
  transition: transform 0.2s ease-out;

  &:hover {
    transform: translate(0, -2vw);
  }
}

.experience-job-description-fade {
  transform: translate(0, -2vw);
  opacity: 1 !important;
}

#experience-job-description {
  width: 40vw;
  position: absolute;
  margin-top: 17vw;
  opacity: 0;
  transition: transform 0.2s ease-out, opacity 0.2s ease-out;

  h3 {
    color: #ffffff;
    margin-bottom: 5px;
  }

  h4 {
    color: #d32223;
    margin: 0;
  }

  p {
    color: #ffffff;
    margin: 0;
    text-align: left;
  }
}
